/**
 * Lauguages
 */


'use strict';

import cookies from '../common/utils/cookies';


const supported = ['zh', 'en', 'jp'];

const lang = {

  zh: {

    // Login
    COMPANY: '',
    USERNAME: '',
    PASSWORD: '',
    LOGIN: '',
    REMEMBER: '',
    FORGOT_PWD: '',
    COPYRIGHT: 'CDP  2015 ',

    // Forgot password
    MOBILE: '',
    CAPTCHA: '',
    GET_CAPTCHA: '',
    RETRY_CAPTCHA: '',
    NEXT: '',
    NEW_PWD: '',
    REPEAT_PWD: '',
    RESET_PWD: '',
    AT_LEAST_6: ' 6 ',
    SAME_PWD: '',
    PWD_RESET_SUCCESS: '',

    WELCOME: '',
    LOGOUT: '',
    PROFILE: '',
    MY_SALARY: '',

    // Validation
    REQUIRED: '',

    // Punctuation
    COMMA: ''
  },

  en: {

    // Login
    COMPANY: 'Company',
    USERNAME: 'User',
    PASSWORD: 'Password',
    LOGIN: 'Login',
    REMEMBER: 'Remember me',
    FORGOT_PWD: 'Forgot password',
    COPYRIGHT: 'CDP  2015 All rights reserved',

    // Forgot password
    MOBILE: 'Mobile',
    CAPTCHA: 'CAPTCHA',
    GET_CAPTCHA: 'Get CAPTCHA',
    RETRY_CAPTCHA: 'seconds',
    NEXT: 'Next',
    NEW_PWD: 'New password',
    REPEAT_PWD: 'Enter password again',
    RESET_PWD: 'Reset Password',
    AT_LEAST_6: 'At least 6 characters',
    SAME_PWD: 'Must the same as password',
    PWD_RESET_SUCCESS: 'Password reset succeeded.',

    WELCOME: 'Welcome',
    LOGOUT: 'Log out',
    PROFILE: 'Profile',
    MY_SALARY: 'My Salary',

    // Validation
    REQUIRED: 'Required',

    // Punctuation
    COMMA: ', '
  },

  jp: {

    // Login
    COMPANY: '',
    USERNAME: '',
    PASSWORD: '',
    LOGIN: '',
    REMEMBER: '',
    FORGOT_PWD: '',
    COPYRIGHT: 'CDP  2015 ',

    // Forgot password
    MOBILE: '',
    CAPTCHA: '',
    GET_CAPTCHA: '',
    RETRY_CAPTCHA: '',
    NEXT: '',
    NEW_PWD: '',
    REPEAT_PWD: '',
    RESET_PWD: '',
    AT_LEAST_6: ' 6 ',
    SAME_PWD: '',
    PWD_RESET_SUCCESS: '',

    WELCOME: '',
    LOGOUT: '',
    PROFILE: '',
    MY_SALARY: '',

    // Validation
    REQUIRED: '',

    // Punctuation
    COMMA: ''
  }
};

const bl = navigator.browserLanguage;
const ls = navigator.languages;
const l = navigator.language;

let defaultLang = cookies.getItem('lang')
                    || (bl && bl.split('-')[0]) // IE11+
                    || (ls && ls[0] && ls[0].split('-')[0])
                    || (l && l.split('-')[0]);

if (supported.indexOf(defaultLang) === -1) {
  defaultLang = 'en';
}


export default {

  setLang(code) {
    defaultLang = code;
    cookies.setItem('lang', code);
  },

  getLang() {
    return defaultLang;
  },

  getItem(key) {
    return lang[defaultLang][key] || '';
  }
};


